*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  font-family: $font-mont;
  color: $color-black;
  font-size: 16px;
  line-height: 1.2;
  &.ovhidden {
    overflow: hidden;
  }
}

body,
html {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
p {
  padding: 0;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

.container {
  max-width: 1440px;
  width: 100%;
  padding: 0 60px;
  margin: 0 auto;
}

main {
  // background-color: #fafbfc;
  // background-color: #000;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.clearfix::after {
  content: "";
  clear: both;
  display: block;
}

.inner-content {
  max-width: 567px;
  width: 100%;
  margin: 0 auto;
}

.button {
  font-size: 16px;
  font-weight: 600;
  border: 1px solid #90b34f;
  color: #90b34f;
  background-color: transparent;
  padding: 0 40px;
  height: 48px;
  line-height: 48px;
  border-radius: 6px;
  text-decoration: none;
}
