.content-wrapper {
  padding: 60px;
  margin-top: 72px;
  margin-left: 250px;
  opacity: 0.9;
  display: flex;
  flex-direction: column;
  // height: 100%;
  // height: 100vh;
  @media (max-width: 1023px) {
    margin-left: 0;
  }
  @media (max-width: 767px) {
    margin-top: 150px;
  }
}

.content-wrapper__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px;
  .back-page {
    position: relative;
    padding-left: 30px;
    text-decoration: none;
    color: #3796f6;
    font-weight: 500;
    &::before {
      content: "";
      position: absolute;
      width: 23px;
      height: 16px;
      background-image: url("../images/arrow-prev.svg");
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }
  }
}

.content-wrapper__nav {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  li {
    cursor: pointer;
    padding: 10px 15px;
    position: relative;
    border-bottom: 4px solid transparent;

    a {
      color: #333;
      text-decoration: none;
      &:hover,
      &.active {
        border-bottom: 4px solid #047df6;
        font-weight: 700;

        &::before {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          margin-top: -4px;
          background-color: #e81717;
          border-radius: 50%;
          width: 8px;
          height: 8px;
        }
      }
    }
  }
}

.content-wrapper__select {
  margin-left: auto;
  select {
    border: none;
    appearance: none;
    padding-right: 30px;
    background-image: url("../images/arrow-select.svg");
    background-repeat: no-repeat;
    background-size: 13px 7px;
    background-position: 99% center;
    outline: none;
  }
}

.content-wrapper__nav-inner {
  display: flex;
  align-items: center;
  margin-right: 40px;
  span {
    font-size: 14px;
    font-weight: 500;
    color: #454545;
  }
  .text {
    margin-right: 20px;
  }
  span.select2 {
    margin-left: 20px;
  }
}

.content-wrapper__input {
  margin-bottom: 40px;
  input {
    border: none;
    background-color: transparent;
    border-bottom: 2px solid #1b87f4;
    max-width: 500px;
    width: 100%;
    padding: 10px;
  }
}
