.history {
    .title {
        margin-bottom: 50px;
    }

    .select {
        display: flex;

        span {
            margin-right: 20px;
        }

        input {
            margin-right: 20px;
            border: none;
        }
    }
}

.history__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;

    input {
        background-color: #fafbfc;
        opacity: 0.9;
    }

    @media (max-width: 767px) {
        align-items: flex-end;
    }
}