.pacient-information__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-weight: 700;
  .item {
    width: calc(50% - 20px);
    background-color: #fff;
    box-shadow: 0px 5px 20px rgba(211, 223, 236, 0.42);
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 20px;
    img {
      width: 100%;
    }

    h3 {
      font-style: 24px;
      color: #333;
      font-weight: 700;
      margin-bottom: 30px;
    }
    @media (max-width: 767px) {
      width: 100%;
    }
  }
}

.pacient-information__content {
  display: flex;
  flex-direction: column;
  padding: 40px;
  p {
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 14px;
  }
  a.read-more {
    border: 1px solid #90b34f;
    border-radius: 6px;
    color: $color-green;
    background-color: #fff;
    padding: 0 40px;
    line-height: 48px;
    align-self: flex-start;
    margin-top: auto;
    cursor: pointer;
    text-decoration: none;
  }
  @media (max-width: 767px) {
    padding: 30px;
  }
}

.pacient-information__article {
  background-color: #fff;
  box-shadow: 0px 5px 20px rgba(211, 223, 236, 0.42);
  border-radius: 6px;
  padding: 40px 0;
  margin-bottom: 50px;
  img {
    margin-bottom: 30px;
    @media (max-width: 767px) {
      margin-bottom: 20px;
    }
  }
  @media (max-width: 767px) {
    padding: 20px 0;
  }
}

.pacient-information__title {
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 10px;
  @media (max-width: 767px) {
    padding: 0 25px;
  }
}

.pacient-information__date {
  font-size: 12px;
  font-weight: 500;
  color: rgba(51, 51, 51, 0.4);
  margin-bottom: 30px;
  @media (max-width: 767px) {
    padding: 0 25px;
    margin-bottom: 20px;
  }
}

.pacient-information__main-text {
  position: relative;
  font-size: 16px;
  font-weight: 500;
  line-height: 150%;
  margin-bottom: 30px;
  &::before {
    content: "";
    position: absolute;
    display: block;
    background-color: #90b34f;
    width: 16px;
    height: 100%;
    left: -40px;
    top: 0;
    border-radius: 6px 0 0 6px;
    @media (max-width: 767px) {
      left: 0;
      width: 8px;
      border-radius: 0;
    }
  }
  @media (max-width: 767px) {
    padding-left: 28px;
    margin-bottom: 20px;
  }
}

.pacient-information__text {
  font-size: 16px;
  font-weight: 500;
  line-height: 150%;
  color: rgba(51, 51, 51, 0.8);
  @media (max-width: 767px) {
    padding: 0 25px;
  }
}
