/*
       
       
     
*/
.statement__info-block p {
    margin-bottom: 5px;
}

/*
      
       
        
*/
.statement__head a.back-page {
    text-decoration: none;
}

/*
     ,  
*/
.sorting,
.sorting_desc,
.sorting_asc {
    cursor: pointer;
}

/*
    , ,
       
     h1 
*/
.content-wrapper__top h1 {
    margin-bottom: 30px;
}

/*
           
*/
#datatable .dataTables_filter {
    display: none;
}

/*
     -    sidebar
*/
.notificationNumber {
    background-color: #fa3e3e;
    border-radius: 2px;
    color: white;

    padding: 1px 3px 1px 3px;
    font-size: 16px;
    margin-left: 15px;

    display: flex;
    align-self: center;
    height: 20px;
}

/*
        ,      
*/
.sidebar__sublist {
    margin-top: 0;
    margin-bottom: 0;
}

/*
        
*/
.sublist > a {
    font-weight: bold;
}

/*
     -      
*/
.dropdown {
    margin-top: 15px;
    cursor: pointer;
}

.dropdown-content {
    margin-left: 40px;
    margin-top: 17px;
    display: none;
    background-color: #1962ad;
    z-index: 1;
}

.dropdown-content a {
    color: white;
    padding: 15px 20px;
    text-decoration: none;
    display: block;
}

.dropdown-content a:hover {
    background-color: #256ab1;
}

.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown:hover .dropbtn {
    background-color: #3e8e41;
}

/*
     -      
*/

/*
     -      main-nav
*/
.searchBox {
    width: 140px;
    cursor: pointer;
}

.searchBox:focus {
    width: 100%;
    cursor: text;
    outline: none;
}

/*
     -      main-nav
*/

/*
           main-nav
*/
a.document-link {
    color: #3796f6;
    cursor: pointer;
    font-size: 16px;
}

p.document-link {
    padding-bottom: 20px;
}

@media (max-width: 1245px) {
    .avatar__subtitle {
        display: none;
    }
}
