.error-form {
  display: none;
  padding: 20px;
  box-shadow: 0px 5px 20px rgba(211, 223, 236, 0.42);
  // background-image: url("../images/error.svg");
  //   background-position: right;
  //   background-repeat: no-repeat;
  // background-size: 61px auto;
  max-width: 490px;
  background-color: #fff;
  margin-bottom: 30px;
  &.active {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.error-form__wrapper {
  display: flex;
  flex-direction: column;
  //   align-items: center;
}

.error-form__title {
  font-weight: 700;
  font-size: 16px;
  color: $color-red;
  margin-bottom: 10px;
  &--mode {
    color: $color-green;
  }
}

.error-form__text {
  font-size: 16px;
  font-weight: 500;
}
