.sidebar {
  position: fixed;
  height: 100%;
  z-index: 100;
  width: 250px;
  top: 72px;
  left: 0;
  height: 100%;
  background-color: #1962ad;
  overflow: auto;
  &--admin {
    background-color: #425262;
  }
  &.active {
    // display: block;
    left: 0;
  }
  @media (max-width: 1023px) {
    // display: none;
    left: -100%;
    transition: 0.3s;
  }
  @media (max-width: 767px) {
    padding-top: 150px;
  }
}

.sidebar__show {
  position: fixed;
  z-index: 50;
  width: 50px;
  top: 72px;
  padding-bottom: 115px;
  left: 0;
  height: 100%;
  background-color: #1962ad;
  overflow: auto;
  color: $color-white;
  .picture {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
  }
  &--green {
    position: absolute;
    background-color: $color-green;
    left: 0;
    width: auto;
    display: none;
  }
  @media (max-width: 1023px) {
    &.active {
      display: block;
      left: 0;
    }
  }
}

.sidebar__list {
  margin-top: 45px;
  // margin-bottom: 100px;
  display: flex;
  flex-direction: column;
  li {
    a {
      text-decoration: none;
      color: #ffffff;
      display: flex;
      height: 100%;
      padding: 20px 60px;
      opacity: 0.67;
      cursor: pointer;
      &:hover,
      &.active,
      &:focus {
        opacity: 1;
        background-color: rgba(255, 255, 255, 0.05);
        font-weight: 700;
      }
    }
    &.active {
      opacity: 1;
      background-color: rgba(255, 255, 255, 0.05);
      font-weight: 700;
    }
  }
  .sublist {
    cursor: pointer;
    ul {
      display: none;
      a {
        padding-left: 80px;
      }
    }
  }
}

.sidebar__nav {
  @media (max-width: 1023px) {
    // display: none;
  }
}

.sidebar__sublist {
