.pacient-aside {
  position: fixed;
  height: 100%;
  background-color: $color-green;
  display: flex;
  flex-direction: column;
  // flex: 1 0 237px;
  max-width: 237px;
  width: 100%;
  padding-top: 30px;
  z-index: 100;
  .main-header__logo {
    max-width: 130px;
    width: 100%;
    align-self: center;
    margin-bottom: 50px;
    a {
      margin-right: 0;
    }
    @media (max-width: 1023px) {
      margin-bottom: 0;
    }
  }
  .pacient-aside__info {
    display: flex;
    flex-direction: column;
    align-self: center;
    // margin-left: auto;
    // margin-right: 20px;
    @media (max-width: 1023px) {
      margin-left: auto;
      margin-right: 20px;
    }
    @media (max-width: 767px) {
      align-self: start;
    }
  }
  .pacient-aside__picture {
    position: relative;
    width: 105px;
    height: 105px;
    border-radius: 50%;
    background-color: #fff;
    // background-image: url("../images/user-logo.png");
    // background-repeat: no-repeat;
    // background-position: center;
    // background-size: contain;
    align-self: center;
    margin-bottom: 15px;
    overflow: hidden;
    // @media (max-width: 767px) {
    //     width: 41px;
    //     height: 41px;
    //     margin-bottom: 0;
    //     &::before {
    //         content: "";
    //         position: absolute;
    //         width: 13px;
    //         height: 13px;
    //         border-radius: 50%;
    //         background-color: #fff;
    //         background-image: url("../images/pencil.svg");
    //         background-size: 6px auto;
    //         background-repeat: no-repeat;
    //         background-position: center;
    //         right: 0;
    //         bottom: 0;
    //     }
    // }
    @media (max-width: 1023px) {
      width: 41px;
      height: 41px;
    }
  }
  .pacient-aside__name {
    text-align: center;
    margin-bottom: 60px;
    color: #ffffff;
    font-size: 16px;
    @media (max-width: 1023px) {
      margin-bottom: 0;
    }
    // @media (max-width: 767px) {
    //   display: none;
    // }
  }
  .pacient-aside__nav {
    li {
      display: flex;
      &:hover,
      &.active {
        background-color: rgba(255, 255, 255, 0.18);
      }
    }
    a {
      padding-left: 60px;
      line-height: 60px;
      text-decoration: none;
      color: #ffffff;
      font-size: 16px;
      width: 100%;
    }
    // @media (max-width: 767px) {
    //     display: none;
    // }
    @media (max-width: 1023px) {
      display: none;
    }
  }

  @media (max-width: 1023px) {
    // justify-content: space-between;
    // flex-direction: row;
    // flex: 0;
    // padding-left: 15px;
    // padding-right: 25px;
    // padding-top: 20px;
    // padding-bottom: 20px;
    // max-width: 100%;
    left: -100%;
    height: auto;
    left: 0;
    position: relative;
    max-width: 100%;
    transition: 0.3s;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px 60px;
    &.active {
      left: 0;
    }
  }
  @media (max-width: 767px) {
    padding: 20px 15px;
    // align-items: center;
  }
}

